.video {
  position: relative;
  max-height: 70rem;
  overflow: hidden;

  .overlay {
    position: absolute;
    height: 100%;
    width: 100%;
    /* Permalink - use to edit and share this gradient. Enlace permanente - úsalo para editar y compartir este gradiente: https://colorzilla.com/gradient-editor/#891c1e+0,9e37f2+67,ffffff+100&0.8+0,0.5+100 */
    background: linear-gradient(135deg,  rgba(137,28,30,0.8) 0%,rgba(158,55,242,0.6) 67%,rgba(255,255,255,0.5) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */

  }

  video {
    display: block;
    width: 100%;
  }
}

.contenido-video {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;

  h2 {
    color: $blanco;
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    @include tablet {
      font-size: 5rem;
    }

    @include desktop {
      font-size: 7rem;
    }

    span {
      background-color: $rosa;
      padding: 1rem 2rem;
      font-size: 2rem;

      @include desktop {
        font-size: 3rem;
      }
    }
  }
}
