.lineup {
  background-color: $rosa;
  padding: $separacion 0;

  h3, p {
    text-align: center;
    color: $blanco;
  }

  .dia {
    text-transform: uppercase;
    margin: $separacion 0;
  }
}

.escenarios-contenedor {
  @include tablet {
    @include grid(2, $separacion)
  }

  .nombre-escenario {
    color: $negro;
    font-size: 3rem;
    font-weight: $delgada;

    &::after {
      content: "";
      width: 20rem;
      height: 10rem;
      display: block;
      margin: 2rem auto 0;
      background-size: 15rem;
      background-repeat: no-repeat;
      background-position: center center;
    }
  }
}

.edm .nombre-escenario {
  &::after {
    background-image: url(../img/icono_edm.png);
  }
}

.rock .nombre-escenario {
  &::after {
    background-image: url(../img/icono_rock.png);
  }
}

.calendario {
  padding: 0;
  list-style: none;

  li {
    padding: calc($separacion / 2);
    font-weight: $bold;

    span {
      font-weight: $regular;
    }
  }
}

.bg-amarillo .calendario li:nth-child(odd) {
  background-color: darken($amarillo, 20);
}

.bg-verde .calendario li:nth-child(odd) {
  background-color: darken($verde, 10);
}