@mixin telefono {
  @media (max-width: $telefono) {
    @content;
  }
}

@mixin tablet {
  @media (min-width: $tablet) {
    @content;
  }
}

@mixin desktop {
  @media (min-width: $desktop) {
    @content;
  }
}

@mixin grid($columnas, $separacion) {
  display: grid;
  grid-template-columns: repeat($columnas, 1fr);
  column-gap: $separacion;
}